import React from 'react';
import styles from './posts.module.css'
import { reduxForm, Field } from 'redux-form';
import { required, maxLengthCreator } from '../../../utils/validators/validator';
import { Textarea } from '../../common/FormControls/FormControls';

const maxLength10 =  maxLengthCreator(10);

const FormPost = (props) => {
    return <form onSubmit={props.handleSubmit}>
        <Field component={Textarea} name='postBody' cols="30" rows="10"  validate={[required, maxLength10]}/>
      
        <button type='submit' className={styles.buttonPost}>Post</button>
    </form>
}
const FormPostRedux = reduxForm({form: 'formPost'}) (FormPost);

const Posts = (props) => {
 
    return <div className={styles.postsContainer}>
        <FormPostRedux onSubmit={props.sendPost}/>
        {props.posts.map((p) => <div key={p.id} className={styles.itemPost}>
            {p.text}
            <span className={styles.likes}>likes: {p.likesCount}</span>
        </div>)}
    </div>
}

export default Posts;