import React from 'react';
import ProfileInfo from './ProfileInfo/ProfileInfo';
import Posts from './Posts/Posts';
import styles from './Profile.module.css';


const Profile = ({sendPost, posts, profile, status, ...props}) => {
    return <div className={styles.profile}>
        <ProfileInfo savePhoto={props.savePhotoThunk} userId={props.match.params.userId} profile={profile} status={status} updateStatus={props.updateStatusThunk} />
        <Posts  sendPost={sendPost} posts={posts}  />
    </div>
}

export default Profile;