
import React, {useState} from 'react';
import styles from './paginator.module.css'




const Paginator = ({ itemsTotalCount, pageSize, currentPage, onPageChanged, portionSize =  10 }) => {
    debugger;
    let pageCount = Math.ceil(itemsTotalCount / pageSize);
    let pages = [];
    for (let i = 1; i <= pageCount; i++) {
        pages.push(i);
    }
    let portionCount = Math.ceil(pageCount / portionSize);
    let [portionNumber, setPortionNumber] = useState(1);
    let leftPortionNumber = (portionNumber - 1) * portionSize + 1;
    let rightPortionNumber = portionNumber * portionSize;


    return <div>
        { portionNumber > 1 && 
            <button onClick={()=>{setPortionNumber(portionNumber-1)}}>RPEV</button>
        }
        {pages
        .filter(p => p >= leftPortionNumber && p <= rightPortionNumber)
        .map(p => {
            const classPage = currentPage === p ? styles.selectedPage : styles.items;
            return currentPage === p
                ? <span className={classPage}>{p}</span>
                : <span className={classPage}
                    onClick={(e) => {
                        onPageChanged(p)
                    }
                    }
                >{p}</span>
        })}
        {portionCount > portionNumber  && 
            <button onClick={() => {setPortionNumber(portionNumber+1)}} >NEXT</button>
        }
    </div>

}

export default Paginator;      